
.nav {
  &__list {
    display: flex;
    padding: 0;
    margin: 0;
    list-style: none;
  }

  &__item {
    margin-left: 20px;

    &:first-child {
      margin-left: 0;
    }
  }

  &__link {
    position: relative;

    display: flex;
    align-items: center;

    height: 60px;

    font-family: "Roboto", sans-serif;
    font-size: 12px;
    font-weight: 500;
    white-space: nowrap;
    text-decoration: none;
    text-transform: uppercase;
    color: $text-accent-color;

    transition: color 0.3s;

    &:hover {
      color: $main-color;
    }

    &.is-active {
      color: $main-color;
    }

    &.is-active::after {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;

      height: 4px;
      border-radius: 2px;
      background-color: $main-color;
    }
  }
}
